 * @Author: geekfeier 
 * @Date: 2018-06-25 09:33:45 
 * @Last Modified by: mikey.zhaopeng
 * @Last Modified time: 2018-10-29 16:09:12
 * 返回@changeDefaultArea方法，$emit出去当前选中地址的省市区id如：[120000,320000,120203]
 * 注意：如果默认渲染省市区，则需传props格式为[120000,320000,120203]；否则传[0]即可 
 */

<template>
  <div>
    <el-cascader :filterable='filterable' placeholder="请选择" :options="options" clearable v-model="newDefaultArea" size="mini" @change="changes" @expand-change="handleItemChange" :props="prop"></el-cascader>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options: [],
      newDefaultArea: [],
    };
  },
  props: {
    defaultArea: {
      type: Array,
    },

    filterable: {
      type: Boolean,
      default: false,
    },
    Level: {
      type: Number,
      default: 3,
    },
    prop: {
      type: Object,
      default: () => {
        return {
          value: "value",
          children: "cities",
        };
      },
    },
  },
  methods: {
    changes(val) {
      this.$nextTick(() => {
        this.$emit("changeDefaultArea", this.newDefaultArea);
      });
    },
    handleItemChange(val) {
      var p_id;
      if (val.length == 1) {
        p_id = val[0];
      }
      if (val.length == 2) {
        p_id = val[1];
      }
      this.$axios({
        method: "get",
        url: this.$api.getAreaListOption,
        params: { p_id: p_id, is_page: 1 },
      }).then((res) => {
        for (var i in this.options) {
          if (
            val.length == 1 &&
            this.options[i].value == val[0] &&
            !this.options[i].cities.length
          ) {
            for (var j in res.data.data) {
              var cities = {};
              cities.label = res.data.data[j].area_name;
              cities.value = res.data.data[j].area_id;
              if (this.Level == 3) {
                cities.cities = [];
              }
              this.options[i].cities.push(cities);
            }
          }
          if (val.length == 2 && this.Level == 3) {
            for (var k in this.options[i].cities) {
              if (
                this.options[i].cities[k].value == val[1] &&
                !this.options[i].cities[k].cities.length
              ) {
                for (var d in res.data.data) {
                  var yArea = {};
                  yArea.label = res.data.data[d].area_name;
                  yArea.value = res.data.data[d].area_id;
                  this.options[i].cities[k].cities.push(yArea);
                }
              }
            }
          }
        }
      });
    },
    initAddress() {
      //区域列表
      let params1 = { level: 2 };
      let _this = this;
      _this.$axMethod.getAreaList(params1).then((res) => {
        var province = {};
        for (var i in res.data.data) {
          var province = {
            label: res.data.data[i].area_name,
            value: res.data.data[i].area_id,
            cities: [],
          };
          _this.options.push(province);
        }
        if (this.defaultArea.length <= 1) return false;
        let params2 = { level: 3, p_id: _this.defaultArea[0] };
        _this.$axMethod.getAreaList(params2).then((res2) => {
          for (var i in _this.options) {
            if (
              _this.options[i].value == params2.p_id &&
              !_this.options[i].cities.length
            ) {
              for (var j in res2.data.data) {
                var cities = {
                  label: res2.data.data[j].area_name,
                  value: res2.data.data[j].area_id,
                  cities: [],
                };
                _this.options[i].cities.push(cities);
              }
            }
          }
          if (this.defaultArea.length <= 2) return false;
          let params3 = { level: 4, p_id: _this.defaultArea[1] };
          _this.$axMethod.getAreaList(params3).then((res3) => {
            for (var i in _this.options) {
              for (var k in _this.options[i].cities) {
                if (
                  _this.options[i].cities[k].value == params3.p_id &&
                  !_this.options[i].cities[k].cities.length
                ) {
                  for (var j in res3.data.data) {
                    var cities = {
                      label: res3.data.data[j].area_name,
                      value: res3.data.data[j].area_id,
                    };
                    _this.options[i].cities[k].cities.push(cities);
                  }
                }
              }
            }
          });
        });
      });
    },
  },
  mounted() {
    if (this.defaultArea.length > 1) {
      this.newDefaultArea = this.defaultArea.filter((val) => val);
      this.initAddress();
    } else {
      let params1 = { level: 2 };
      this.$axMethod.getAreaList(params1).then((res) => {
        var province = {};
        for (var i in res.data.data) {
          var province = {
            label: res.data.data[i].area_name,
            value: res.data.data[i].area_id,
            cities: [],
          };
          this.options.push(province);
        }
      });
    }
  },
  watch: {
    defaultArea: {
      handler(val, oldVal) {
        this.options = [];
        this.newDefaultArea = this.defaultArea;
        this.initAddress();
      },
      deep: true,
    },
  },
};
</script> 